import * as React from 'react';
import BaseElementProps from '../BaseElementProps';
import Set from '../raphael/Set';
import Rect from '../raphael/Rect';
import Text from '../raphael/Text';

export interface PoolProps extends BaseElementProps {
  text?: string;
  textTransform?: string;
  radius?: number;
}

export default class Pool extends React.Component<PoolProps, any> {
  static defaultProps = {
    x: 0,
    y: 0,
    stroke: '#000',
    fill: 'none',
    strokeWidth: 1,
    radius: 0,
    textTransform: 'r270',
  }

  render() {
    const { x, y, width, height, radius, stroke, strokeWidth, fill, fillOpacity,
      text, textTransform } = this.props;

    return (<Set>
      <Rect
        x={x}
        y={y}
        width={width}
        height={height}
        r={radius}
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill={fill}
        fillOpacity={fillOpacity}
      />
      <Text
        x={x + 14}
        y={y + height / 2}
        text={text}
        transform={textTransform}
      />
    </Set>);
  }
}



